import React from 'react';
import styled from 'styled-components';
import { Link as RouterLink } from 'react-router-dom';
import { BouncingText } from '../../Components';

const Header = props => (
  <div className={props.className}>
    <div className="container">
      <div className="my-name"><BouncingText text="WelcometoShizuku'spage!" click hover delay={20} duration={250} /></div>
      <Link to="/">Home</Link>
      <Link to="/me">About Me</Link>
      <Link to="/collection">Collection</Link>
      <Link to="/articles">Articles</Link>
    </div>
  </div>
);

const Link = ({ children, to }) => <div className="header-link"><RouterLink to={to}>{children}</RouterLink></div>;

export default styled(Header)`
  > .container {
    display: flex;
    justify-content: flex-end;
  }
  .header-link {
    padding: 0px 20px;
    font-size: 1.1em;
  }
  .my-name {
    font-size: 1.1em;
    padding: 0px 20px;
    flex-grow: 1;
    letter-spacing: 1px;
  }
`;



