import React from 'react';
import styled from 'styled-components';
import moon from './moon.png';
import sun from './sun.png';
import grass from './grass.png';

const Background = (props) => {
  const { className } = props;
  const dayTime = new Date().getHours();
  const background = (dayTime >= 6 && dayTime < 18) ? <Day /> : <Night />;
  return (
    <div className={className}>
      <Day />
    </div>
  );
};

const Day = () => (
  <div className="day">
    <Sun />
    <Grass />
  </div>
);
const Night = () => (
  <div className="night">

    <Moon />
  </div>
);

const Moon = () => <img className="moon" src={moon} alt="moon" />;
const Sun = () => <img className="sun" src={sun} alt="sun" />;
const Grass = () => <img className="grass" src={grass} alt="grass" />;

export default styled(Background)`
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  z-index: -1;

  .night {
    position: relative;
    width: 100%;
    height: 100%;
    background: rgb(70, 68, 68);
  }
  .day {
    position: relative;
    width: 100%;
    height: 100%;
  }

  .moon {
    position: fixed;
    width: 10%;
    left: 80%;
    top: 10%;
    animation: turning-moon 17s linear infinite;
  }
  .sun {
    position: fixed;
    width: 10%;
    left: 80%;
    top: 10%;
    animation: turning-sun 4s infinite;
  }
  .grass {
    position: fixed;
    left: 0px;
    bottom: 0px;
    height: 15%;
    width: 100%;
  }

  @keyframes turning-moon {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
  @keyframes turning-sun {
    0% { transform: rotate(0deg); }
    25% { transform: rotate(10deg); }
    50% { transform: rotate(0deg); }
    75% { transform: rotate(-10deg); }
    100% { transform: rotate(0deg); }
  }
`;



